@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: 'Poppins', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.contact-container {
    margin: 0 auto;
    text-align: center;
}

.contact-container h4 {
    text-align: center;
    font-size: 2.5rem;
    background: linear-gradient(90deg, #FC466B 30%, #3F5EFB 75%);
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-text-fill-color: transparent;
}

.contact-form {
    display: inline-block;
    width: 45%;
}

.contact-form h5 {
    text-align: center;
    font-size: 1.5rem;
    background: linear-gradient(90deg, #FC466B 30%, #3F5EFB 75%);
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-text-fill-color: transparent;
}

.about-me {
    display: inline-block;
    width: 45%;
    vertical-align: top;
}

.about-me h5 {
    background: linear-gradient(90deg, #FC466B 30%, #3F5EFB 75%);
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 1.5rem;
}
.nav {
    width: 100%;
    position: fixed;
    background-color: white;
    border-bottom: 2px solid goldenrod;
    z-index: 99999;
}

.nav-list {
    list-style-type: none;
    text-align: center;
    padding: 1%;
    font-size: 0.85em;
}

.list-items {
    display: inline-block;
    padding: 1%;
}

@-webkit-keyframes colorChange {
    from {color: grey;}
    to {color: goldenrod;}
  }

@keyframes colorChange {
    from {color: grey;}
    to {color: goldenrod;}
  }

.list-items:not(:first-child) a:hover  {
    -webkit-animation-name: colorChange;
            animation-name: colorChange;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    color:goldenrod;
}

.list-items:nth-child(1) {
    text-align: left;
    float: left;
    padding-right:15%;
    padding-left: 5%;
}

.list-items:nth-child(1) a {
    font-size: 1.4rem;
    color:black;
}

.list-items a span:nth-child(1) {
    color:goldenrod;
    font-weight: bold;
}

.list-items a {
    text-decoration: none;
    color: grey;
    font-size: 1.25em;

}

@media only screen and (max-width: 768px) { 
    .list-items:not(:nth-child(1)) {
        display: none;
    }

}
.main {
    padding-top: 10%;
    width: 80%;
    margin: 0 auto;
    text-align: center;
    padding-bottom: 8%;
}

.left-side {
    display: inline-block;
    width: 25%;
    height: 25%;
    border-radius: 50%;
    
    /* background-color: rgb(224, 224, 224); */
}

.left-side img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    position:relative;
    top: 11px;
    right: 20px;
    border: 14px solid rgb(255, 255, 255);
    /* background: linear-gradient(90deg, #FC466B 0%, #3F5EFB 100%); */
    background: linear-gradient(90deg, #FC466B 0%, #3F5EFB 100%);
    padding:1%;
    box-shadow: 0px 0px 8px 3px rgba(196,188,196,1);
}

.right-side {
    display: inline-block;
    width: 45%;
    vertical-align:top;
    padding-top: 5%;
    padding-left: 7%;
}

.right-side h3 {
    color: grey;
    text-align: left;
    font-size: 1rem;
    font-weight: 300;
    padding: 10px 0px;
}

.right-side h1 {
    text-align: left;
    font-weight: 600;
    font-size: 2rem;
    padding: 0px 0px 15px 0px;
}

.right-side p {
    color: rgb(168, 168, 168);
    font-weight: 200;
    text-align: left;
    padding-bottom: 3%;
}

.button-contact {
    float: left;
    border: 2px solid goldenrod;
    background-color: white;
    color: goldenrod;
    font-weight: 300;
    padding: 10px 40px;
    border-radius: 35px;
}

.button-social {
    float: left;
    border: 2px solid goldenrod;
    background-color: white;
    color: goldenrod;
    font-weight: 300;
    padding: 4px 7px;
    border-radius: 35px;
    margin: 0 14px;
}


@-webkit-keyframes example {
    from {background-color: white;}
    to {background-color: goldenrod;}
  }


@keyframes example {
    from {background-color: white;}
    to {background-color: goldenrod;}
  }

.right-side button:hover {
    -webkit-animation-name: example;
            animation-name: example;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
    background-color: goldenrod;
    color: white;
}

@media only screen and (max-width: 1000px) {
    .left-side{width: 40%; height: 40%;}
}

@media only screen and (max-width: 750px) {
    .main{width: 80%;}
    .left-side{width: 100%; margin: 4% auto;}
    .right-side{width: 100%; text-align: center; margin: 0 auto;}
}
.skills {
    padding:10% 0;
    width: 80%;
    margin: 0 auto;
    /* text-align: center; */
}

.education-skills {
    /* text-align: center; */
    display: inline-block;
    width: 46%;
    padding:2%;
}
.skills-style {
    /* text-align: center; */
    display: inline-block;
    width: 46%;
    padding: 2%;
    vertical-align: top;
}

.skills-style h4 {
    color: grey;
    font-size: 2em;
    letter-spacing: 1px;
    background: linear-gradient(90deg, #FC466B 0%, #3F5EFB 55%);
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-text-fill-color: transparent;
}

/* Start of Skill section stylize */


.skill-set {
    display: inline-block;
    width: 100%;
    text-align: left;
    padding: 3% 0;
    border-bottom: 1px solid rgba(218, 165, 32, 0.425);
    /* border-left: 1px solid rgb(241, 241, 241); */
}

.skill-period {
    position: relative;
    right: 40px;
    display: inline-block;
    border: 1px solid goldenrod;
    padding: 7px;
    margin: 3% 2%;
    border-radius: 40px;
}

.university {
    color: grey;
    font-weight: 300;
}

.faculty {
    font-size: 1.25rem;
    padding-bottom: 12px;
}

.acquired-skills {
    color: grey;
    font-size: 0.9rem;
    font-weight: 300;
}

.acquired-skills ul li {
    display: inline-block;
    width: 35%;
    padding: 4px 0;
}

.courses {
    display: block;
    color: goldenrod;
    font-weight: 400;
}


@media only screen and (max-width: 810px) { 
    .skills {
        text-align: center;
    }
    
    .skills-style {
        margin: 0 auto;
        text-align: center;
        width: 80%;
    }

    .skills-style h4 {
        background: linear-gradient(90deg, #FC466B 30%, #3F5EFB 75%);
        -webkit-background-clip: text;
                background-clip: text;
        -webkit-text-fill-color: transparent;
    }

    .acquired-skills ul li { 
        display: block;
        width: 80%;
    }

}

/* End of Skill section stylize */
.facts {
    width: 75%;
    margin: auto;
    padding-bottom: 5%;
    text-align: center;
}

.facts-heading {
    color: grey;
    font-size: 2em;
    letter-spacing: 1px;
    background: linear-gradient(90deg, #FC466B 0%, #3F5EFB 12%);
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-text-fill-color: transparent;
    padding: 4%;
    text-align: left;
}

.fun-fact {
    display: inline-block;
    width: 170px;
    height: 170px;
    padding: 3%;
    margin: 20px;
    border: 1px solid goldenrod;
    /* background: linear-gradient(90deg, #FC466B 0%, #3F5EFB 12%); */
    border-top-left-radius: 25%;
    border-top-right-radius: 25%;
    border-bottom-right-radius: 90%;
    border-bottom-left-radius: 90%;
    text-align: center;
    /* background-color: rgb(247, 247, 247); */
    box-shadow: inset 0px 0px 2px 1px rgba(0,0,0,0.25);
}

.fun-fact p {
    padding: 1%;
}

.fun-fact i {
    /* color: rgb(32, 218, 141); */
    color: goldenrod;
    font-size: 2.4rem;
    padding: 15px 0;
}

.fun-fact p:nth-child(2) {
    font-size: 1.15rem;
    font-weight: 500;
    padding-bottom: 4px;
}

.fun-fact p:nth-child(3) {
    font-size: 2rem;
    font-weight: 500;
    padding-bottom: 15px;
    /* color: rgba(32, 218, 131, 0.541); */
    color: goldenrod;
}

.fun-fact:hover {
    box-shadow: inset 0px 0px 15px 4px goldenrod;
}

.opalq {
    font-size: 2rem;
}

/* /* @media only screen and (max-width: 750px) { 
    .
} */

@media only screen and (max-width: 750px) { 
    .facts {
        width: 100%;
    }

    .facts-heading {text-align: center;}
} 
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

